a {
  text-decoration: none;
  border: none;
  background: none;
  color: inherit;
}

.navbar-container {
height: 75px;
width: 100%;
display: -ms-flexbox;
display: flex;
-ms-flex-pack: distribute;
    justify-content: space-around;
-ms-flex-align: center;
    align-items: center;
}

.navbar-title {
font-size: 40px;
color: #ccc;
text-rendering: optimizeLegibility;
font-weight: bold
}

.navbar-title:hover {
cursor: pointer;
}

.navbar-categories {
display: -ms-flexbox;
display: flex;
-ms-flex-pack: distribute;
    justify-content: space-around;
width: 650px;
color: gray;
font-weight: bold;
text-rendering: optimizeLegibility;
font-size: 20px;
}

.navbar-category {
-webkit-transition: color 0.5s;
-o-transition: color 0.5s;
transition: color 0.5s;
}

.navbar-category:hover {
cursor: pointer;
color: #ccc;
}



